import api from './api';

export const getTwitter = async (str: string) => {
   const token =
      'AAAAAAAAAAAAAAAAAAAAAKAYagEAAAAA7WzrZGu%2BtJgxRbgLuho4xaI4hVc%3DoXPHjHkRfN5fWkCGL1dYZeqegReTS50InA4DfMvTCbE5yQXMNT';

   const url =
      'https://cors.bridged.cc/https://api.twitter.com/2/tweets/search/recent';

   const get = await api.get(url, {
      headers: {
         Authorization: `Bearer ${token}`,
         'Content-Type': 'application/x-www-form-urlencoded',
      },
      params: {
         query: str,
         max_results: 10,
      },
   });

   console.log(get);
};
